import { fetchFromAPI } from './api';

// Cache for search suggestions
const suggestionsCache = new Map<string, { suggestions: string[]; timestamp: number }>();
const CACHE_DURATION = 1000 * 60 * 5; // 5 minutes

export async function getSearchSuggestions(query: string): Promise<string[]> {
  if (!query || query.length < 2) return [];

  // Check cache first
  const cached = suggestionsCache.get(query);
  if (cached && Date.now() - cached.timestamp < CACHE_DURATION) {
    return cached.suggestions;
  }

  try {
    const response = await fetchFromAPI('suggestions', {
      method: 'POST',
      body: JSON.stringify({ query })
    });

    const suggestions = response.suggestions || generateFallbackSuggestions(query);

    // Cache the results
    suggestionsCache.set(query, {
      suggestions,
      timestamp: Date.now()
    });

    return suggestions;
  } catch (error) {
    console.error('Error generating suggestions:', error);
    return generateFallbackSuggestions(query);
  }
}

function generateFallbackSuggestions(query: string): string[] {
  return [
    `🌎 ${query} City Guide`,
    `🎯 Best Places in ${query}`,
    `🎯 ${query} Local Food`,
    `🎯 ${query} Cultural Sites`,
    `🎯 ${query} Hidden Gems`
  ];
}