import type { Destination } from '../types/destination';
import { destinations } from '../data/destinations';

// Cache implementation
const searchCache = new Map<string, { data: Destination; timestamp: number }>();
const CACHE_DURATION = 1000 * 60 * 60 * 24; // 24 hours

const API_URL = import.meta.env.PROD ? '/.netlify/functions' : 'http://localhost:8888/.netlify/functions';

export async function getSearchResults(query: string): Promise<Destination> {
  // Check cache first
  const cached = searchCache.get(query);
  if (cached && Date.now() - cached.timestamp < CACHE_DURATION) {
    return cached.data;
  }

  try {
    const response = await fetch(`${API_URL}/search`, {
      method: 'POST',
      headers: {
        'Content-Type': 'application/json',
      },
      body: JSON.stringify({ query })
    });

    if (!response.ok) {
      throw new Error('Failed to fetch search results');
    }

    const result = await response.json();

    // Cache the results
    searchCache.set(query, {
      data: result,
      timestamp: Date.now()
    });

    return result;
  } catch (error) {
    console.error('Error fetching search results:', error);
    // Fallback to static data
    const fallback = destinations.find(dest => 
      dest.name.toLowerCase().includes(query.toLowerCase())
    ) || destinations[0];
    
    return fallback;
  }
}